.contact {
  width: 100%;
  height: 100%;
  background-color: transparent;
  display: flex;
  justify-content: center;

  .left {
    flex: 0.4;
    margin-left: 2rem;
    align-self: start;
    padding-top: 5.5rem;

    @media (max-width: 768px) {
      iframe {
        display: none;
      }
    }

    iframe {
      border-radius: 20px;
    }
  }

  .right {
    flex: 0.6;
    height: 100%;
    display: flex;
    flex-direction: column;

    .formcontainer {
      width: 100%;
      height: 100%;
      margin: 0 auto;
      display: flex;
      justify-content: center;
      flex-direction: column;
      align-items: center;

      .social-links {
        width: 60%;
        display: flex;
        justify-content: center;
        align-items: center;
        list-style: none;
        padding: 0.5rem 0;

        img {
          width: 20px;
          height: auto;
          margin: 0;
          margin-left: 1rem;
        }
      }

      form {
        width: 60%;
        min-height: auto;
        box-shadow: -1px -1px 2px 2px #ccc;
        display: flex;
        flex-direction: column;
        justify-content: center;

        .forminput {
          width: auto;
          margin: 1rem;
          display: flex;
          flex-direction: column;
        }

        input {
          padding: 0.5rem 1rem;
        }


        input:invalid[focused="true"] {
          display: block;
          border: 1px solid red;
        }

        span {
          color: red;
          margin: 0.5rem;
          display: none;
        }

        input:invalid[focused="true"] ~ span {
          display: block;
        }

        button {
          margin: 1rem 0.5rem;
          background-color: #ffc01c;
          color: #fff;
          padding: 0.5rem;
          border: none;
          border-radius: 2rem;
        }
      }
    }

    footer {
      width: 100%;
      height: auto;
      align-self: end;
      display: flex;
      justify-content: center;
      align-items: end;
      transform-origin: bottom;
      transition: all 2s ease;
      margin-bottom: 1rem;

      a {
        text-decoration: none;
        color: #000;

        .downarrow {
          font-size: 4rem;
          align-self: center;

          &:hover {
            animation: none;
          }
        }
      }
    }
  }

  @media (max-width: 768px) {
    flex-direction: column;
  }
}
