@import url('https://fonts.googleapis.com/css2?family=Nanum+Brush+Script&family=Roboto:ital,wght@0,100;0,300;0,400;0,500;0,700;1,100;1,300&display=swap');

.aboutme {
  width: 100%;
  height: 100%;
  background-color: transparent;
  display: flex;
  justify-content: center;
  align-items: center;

  @media (max-width: 768px) {
    flex-direction: column;
    margin-top: 2rem;

    .left {
      width: 100%;
      margin-left: 40%;
    }
  }

  .left {
    flex: 0.3;
    width: 100%;
    padding-left: 2rem;
    padding-right: 4rem;

    h1 {
      font-size: 3rem;
      margin-right: 4rem;

      @media (max-width: 768px) {
        font-size: 2rem;

        span {
          transform: translateX(-10%);
          font-size: 150%;
        }
      }

      span {
        transform: translateX(-5%);
        font-size: 150%;
      }
    }

    span {
      display: inline-block;
      font-size: 1.5rem;
      font-style: italic;
      padding: 1rem 3rem 3rem 0;
      color: #ffc10c;
      margin: 1rem;
      margin-left: 0;
      margin-right: 1rem;
    }

    .statistics {
      width: 100%;
      height: auto;
      display: flex;
      justify-content: start;
      align-items: start;
      font-size: 0.8rem;
      margin-left: -6px;

      li {
        display: inline-block;
        margin: 0;
        padding: 0 2rem;
        margin-right: 1rem;
        border-radius: 1.5rem;
        font-family: 'Roboto';
        text-align: center;
        position: relative;
        animation: movedown 5s ease forwards;

        small {
          font-size: 2rem;
          font-family: inherit;
          transform-origin: 0;
          align-self: center;
          font-style: italic;
          font-weight: 700;

          &::after {
            position: absolute;
            width: 50%;
            height: 50%;
            border-radius: 50%;
            content: '';
            bottom: -15px;
            left: 25%;
            background-color: #ffc10c;
            z-index: -1;
            transform: rotate3d(15deg);
          }
        }
      }

      @media (max-width: 768px) {
        justify-content: start;
        margin-left: -10px;
      }
    }

    .resume {
      display: inline-block;
      margin: 2rem 1rem 0 0;
      padding: 0.7rem 2rem;
      border-radius: 1.5rem;
      font-size: 2rem;
      border: none;
      background-color: #ffc10c;
      align-self: start;
      cursor: pointer;

      &:hover {
        background-color: rgba(255, 193, 12, 0.7);
      }

      @media (max-width: 768px) {
        margin-left: 1.7rem;
      }
    }
  }

  .right {
    width: 100%;
    height: 100%;
    flex: 0.7;
    padding: 0 2rem;
    align-self: start;
    font-family: inherit;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: flex-start;
    font-family: 'Nanum Brush Script', cursive;

    h1 {
      align-self: center;
      margin-bottom: 0;
      margin-top: 1rem;
      padding-bottom: 0;
      padding-top: 2rem;
      position: relative;

      @media (max-width: 768px) {
        font-size: 2rem;
      }
    }

    span {
      align-self: center;
      color: #ffc10c;
      font-size: 1.2rem;
    }

    footer {
      width: 100%;
      height: auto;
      align-self: center;
      display: flex;
      justify-content: center;
      align-items: start;
      transform-origin: top;
      transition: all 2s ease;
      margin-bottom: 1rem;

      a {
        text-decoration: none;
        color: #000;

        .downarrow {
          font-size: 4rem;
          align-self: center;
          animation: moveArrow 4s ease infinite;

          &:hover {
            animation: none;
          }
        }
      }
    }
  }

  @keyframes movedown {
    0% {
      transform: translateY(-10px);
      opacity: 0;
    }

    50% {
      transform: translateY(5px);
      opacity: 0.5;
    }

    100% {
      transform: translateY(0);
    }
  }
}
