.portfolio {
  width: 100%;
  height: 100%;
  background-color: transparent;
  display: flex;
  flex-direction: column;
  justify-content: start;
  padding: 1rem 2rem;
  padding-top: 2rem;

  .wrapper {
    width: 100%;
    height: auto;
    display: flex;
    justify-content: center;
    align-items: center;
    padding-top: 1.5rem;

    @media (max-width: 768px) {
      flex-direction: column;
    }

    .portfolio-tabs-section {
      margin-top: 0;
      height: 100%;
      flex: 0.4;
      display: flex;
      flex-direction: column;
      justify-content: start;
      align-items: center;

      h1 {
        font-size: 6rem;
        margin-bottom: 0;
      }


      .portfolio-tabs {
        height: 100%;
        width: 100%;
        display: flex;
        flex-wrap: wrap;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        list-style: none;

        @media (max-width: 768px) {
          display: grid;
          grid-template-columns: 1fr 1fr;
          grid-template-rows: auto;
          place-items: center;
          gap: 0.5rem;


          .itemlist {
            font-size: 1rem;
          }
        }

        .itemlist {
          margin-bottom: 3rem;
          color: #ffc10c;
          font-size: 1.5rem;
          cursor: pointer;
          transition: all 2s ease;

          &:hover {
            transform: scale(1.1);
          }

          &.active {
            display: block;
            padding: 1rem;
            background-color: #ffc10c;
            color: #fff;
            border-radius: 1rem;
          }
        }
      }
    }

    .projects {
      flex: 0.6;
      height: 100%;
      width: 100%;
      display: flex;
      flex-wrap: wrap;
      justify-content: start;
      align-items: start;

      @media (max-width: 768px) {
        width: 100%;
        height: 100%;
        order: 0;
        display: grid;
        grid-template-columns: 1fr 1fr;
        grid-template-rows: auto;
      }

      .card {
        width: 200px;
        height: 200px;
        margin: 1.5rem;
        transition: all 2s ease;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        position: relative;

        h6 {
          margin-bottom: 2rem;
          font-size: 1.5rem;
        }

        img {
          width: 100%;
          height: 100%;
          object-fit: contain;
        }

        &:hover {
          transform: scale(2);
          z-index: 3;
        }

        span {
          position: absolute;
          display: inline-block;
          cursor: pointer;
          width: auto;
          font-size: 1.2rem;
          height: 20px;
          padding: 3px 4px 5px;
          background-color: #ffc10c;
          border-radius: 20px;
          top: 25px;
          right: 8px;

          a {
            text-decoration: none;
            color: #fff;
          }
        }

        .demoicons {
          color: #ffc10c;
          font-size: 1.5rem;
        }

        @media (max-width: 768px) {
          width: 50%;
          height: auto;
        }
      }

      footer {
        width: 100%;
        height: auto;
        align-self: center;
        display: flex;
        justify-content: center;
        align-items: start;
        transform-origin: top;
        transition: all 2s ease;
        margin-bottom: 1rem;

        a {
          text-decoration: none;
          color: #000;

          .downarrow {
            font-size: 4rem;
            align-self: center;
            animation: moveArrow 4s ease infinite;

            &:hover {
              animation: none;
            }
          }
        }
      }
    }
  }
}
