.intro {
  width: 100%;
  height: 100%;
  display: flex;
  background: transparent;

  @media (max-width: 768px) {
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin-bottom: 6rem;
  }

  .right-section {
    width: 100%;
    height: 100%;
    flex: 0.4;
    justify-content: center;
    align-items: end;
    padding: 1rem 2rem;

    .pic-section {
      width: 100%;
      height: 100%;
      position: relative;

      &::before {
        position: absolute;
        right: -4rem;
        bottom: 2rem;
        width: 100%;
        height: 10%;
        border-radius: 50%;
        transform: translateY(-50%);
        content: 'Minimalistic design, less distraction, more business';
        font-size: 1.5rem;
        color: #ffc10c;
        font-weight: 300;
        display: flex;
        justify-content: center;
        align-items: center;

        @media (max-width: 768px) {
          display: none;
        }
      }

      img {
        position: absolute;
        top: -15%;
        left: -40px;
        width: 100%;
        height: 100%;
        background-color: transparent;
        object-fit: cover;
        align-self: center;
        z-index: 4;
      }
    }

    @media (max-width: 768px) {
      order: 0;
      width: 100%;
      height: 100%;
      padding: 1rem 0;

      .pic-section {
        width: 100%;
        height: 100%;
        position: relative;

        img {
          position: absolute;
          top: -100px;
          left: -70px;
          width: 100%;
          height: 200%;
          object-fit: contain;
        }
      }
    }
  }

  .left-section {
    flex: 0.6;
    width: 100%;
    height: 100%;
    display: flex;
    padding-left: 3rem;
    margin-right: 1rem;
    margin-top: 1rem;
    flex-direction: column;
    justify-content: center;
    align-items: end;

    h1 {
      font-size: 10rem;
      font-weight: 700;
      align-self: end;
    }

    h2 {
      font-size: 2.5rem;
      font-weight: 400;
      color: #ffc10c;
    }

    h3 {
      font-size: 3rem;
      align-self: end;
      margin-left: 2rem;
    }

    span {
      margin-left: 1rem;
      color: #ffc10c;
    }

    ul {
      width: 100%;
      height: auto;
      display: flex;
      list-style: none;
      align-items: baseline;
      justify-content: center;

      @media (min-width: 768px) {
        width: auto;
        justify-content: end;
      }

      padding: 0.5rem;

      li {
        margin: 1.5rem 1rem;

        .icons {
          color: #1c1c1c;
          font-size: 2rem;
        }

        a {
          img {
            width: 33px;
            height: auto;
          }
        }

        .resume {
          display: inline-block;
          margin: 1.5rem 1rem 1rem 0;
          padding: 0.7rem 2rem;
          border-radius: 1.5rem;
          font-size: 2rem;
          border: none;
          background-color: #ffc10c;
          align-self: start;
          cursor: pointer;

          &:hover {
            background-color: rgba(255, 193, 12, 0.7);
          }

          @media (max-width: 768px) {
            margin-left: 1.7rem;
          }
        }
      }
    }

    footer {
      width: 100%;
      height: auto;
      align-self: center;
      display: flex;
      justify-content: end;
      align-items: end;
      transform-origin: top;
      padding-right: 6rem;
      transition: all 2s ease;

      a {
        text-decoration: none;
        color: #000;

        .downarrow {
          font-size: 4rem;
          align-self: center;
          animation: moveArrow 4s ease infinite;

          &:hover {
            animation: none;
          }
        }
      }
    }

    @media (max-width: 768px) {
      width: 100%;
      height: 100%;
      order: 1;
      display: flex;
      align-items: center;
      justify-content: center;
      margin-top: 2rem;
      padding-left: 0;

      h1 {
        font-size: 500%;
        align-self: center;
      }

      h2,
      h3 {
        margin: 0;
        font-size: 200%;
        align-self: center;
      }

      footer {
        padding-right: 50%;
      }
    }
  }

  @keyframes moveArrow {
    0% {
      transform: translate(0, 0);
      opacity: 0.2;
    }

    50% {
      transform: translate(0, 2px);
      opacity: 0.5;
    }

    100% {
      transform: translate(0, 10px);
      opacity: 1;
    }
  }
}




